/* Custom styles for the Sorted IDE */

/* Base theme colors */
:root {
    --bg: #0f0f0f;
    --bg-secondary: #1a1a1a;
    --bg-toolbar: #27282b;
    --bg-button-hover: #868686;
    --border-color: #222227;
    --text-primary: #e0e0e0;
    --text-secondary: #a0a0a0;
    --run-button: #00a65a;
    --accent-color: #2577f1;
  }
  
  /* Main container */
  .ide-container {
    background-color: var(--bg-main);
    color: var(--text-primary);
  }
  
  /* buttons */
  .tools{
    background-color: var(--bg-toolbar);
    height: 100%;
    border-radius: 5px;
  }
  
  .console-tools{
    background-color: var(--bg-secondary)
  }
  .toolbar{
    display: flex;
    margin-bottom: 20;
  }
  
  .button {
    transition: background-color 0.2s, color 0.2s;
    padding: 5;
  }
  
  .button:hover {
    background-color: var(--bg-button-hover);
  }
  
  /* run button */
  .run-button {
    position: relative;
    overflow: hidden;
    background-color: var(--);
  }

  /* Run button pulse effect on execution */
  @keyframes pulse {
    0% {
      box-shadow: 0 0 0 0 rgba(21, 128, 61, 0.7);
    }
    70% {
      box-shadow: 0 0 0 6px rgba(21, 128, 61, 0);
    }
    100% {
      box-shadow: 0 0 0 0 rgba(21, 128, 61, 0);
    }
  }
  
  .run-button-executing {
    animation: pulse 1.5s infinite;
  }

    /* Style for language selector dropdown */
  .lang-select {
    appearance: none;
    border: none;
    padding: 4px 12px 4px 4px;
    background-repeat: no-repeat;
    background-position: right 4px center;
  }
  
  /* Toolbar button icons */
  .toolbar-icon {
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }
  
  .toolbar-icon svg {
    width: 16px;
    height: 16px;
  }
  
  /* IDE */

    .monaco-editor {
    padding-top: 5px;
  }
  
  .monaco-editor .margin {
    background-color: var(--bg-secondary) !important;
  }
  
  /* Custom line number styles */
  .monaco-editor .line-numbers {
    color: var(--text-secondary) !important;
  }

  /* Terminal/output styling */
  .terminal-output {
    line-height: 1.5;
    background-color: white
  }
  
  .terminal-output pre {
    margin: 0 0 4px 0;
  }
  
  /* Add subtle highlight to current line in editor */
  .monaco-editor .current-line {
    background-color: rgba(255, 255, 255, 0.05) !important;
    border: none !important;
  }

    /* Editor focus outline */
    .monaco-editor:focus-within {
        outline: 1px solid rgba(255, 255, 255, 0.3);
      }

  /* Editor focus outline */
  .monaco-editor:focus-within {
    outline: 1px solid rgba(255, 255, 255, 0.3);
  }

  .statusbar{
    background-color: var(--bg-secondary);
  }
  
      
  
  /* Logo styling */
  .ide-logo {
    font-weight: bold;
    letter-spacing: -0.5px;
  }
  
  .ide-logo::before {
    content: '<';
    color: var(--accent-color);
  }
  
  .ide-logo::after {
    content: '/>';
    color: var(--accent-color);
  }
  
